/*
 *  Account Page
 *    Styles for account page.
 *
 *  Dependencies:
 *
 *  Version:
 *    1.1.0 - 2018/08/06
 */

@import '../base.scss';
@import '../components/accounts/_account-navigation';
@import '../components/accounts/_addresses';
@import '../components/accounts/_addresses-form';
@import '../components/accounts/_orders';
@import '../components/accounts/_addresses';

.c-account {
  &__header {
    background-color: $s-color--background-tertiary;

    &-inner {
      @include t-gutters;
      padding-top: t-to-rem(130);
      padding-bottom: t-to-rem(40);
    }

    &-heading {
      color: $s-color--font-6;
      text-align: center;
    }
  }

  //styles for banner
  &__banner {
    position: relative;
    width: 95%;
    margin: 1.25rem auto 1.25rem;
    display: block;

    &-image-container {
      padding-bottom: 48%;
    }

    &-content {
      @include t-absolute-center-x-y();
      width: 80%;
      text-align: center;
    }

    &-title {
      color: white;
    }

    &-subtitle {
      margin-top: 1.25rem;
      color: white;
    }
  }

  &__inner {
    padding-top: 8%;
    padding-bottom: 8%;
    @include t-gutters;
  }

  .o-form__input-group {
    margin: t-to-rem(16) 0;
  }

  &__text {
    width: 100%;
    text-align: center;
  }

  &__detail {
    margin-bottom: 1.25rem;

    p {
      border: 1px solid $s-color--error;
      margin: .5rem 0;
    }

    &-title {
      display: block;
      font-weight: bold;
      margin-bottom: .5rem;
    }
  }

  &__details {
    padding-bottom: t-to-rem(32);
    border-bottom: 2px solid rgba($s-color--background-secondary, 0.1);
    margin-bottom: t-to-rem(32);

    &-title {
      margin-top: 0;
      margin-bottom: t-to-rem(20);
    }
  }

  &__orders {
    margin-top: 2rem;

    &-container {
      overflow: auto;

      table {
        border-spacing: 0 0;
        width: 100%;
      }

      th,
      td {
        white-space: nowrap;
        vertical-align: baseline;
        text-align: left;
        padding-right: 1.5rem;
        padding-bottom: .5rem;
      }
    }
  }

  &__orders,
  &__order-table {
    border-collapse: collapse;
    width: 100%;

    th,
    td {
      text-align: center;

      &:first-of-type {
        text-align: left;
      }

      &:last-of-type {
        text-align: right;
      }
    }

    tbody td {
      padding-top: 1em;
      padding-bottom: 1em;
    }

    thead tr:last-of-type th {
      border-top: 2px solid rgba($s-color--background-secondary, 0.1);
      padding-bottom: 1em;
    }
  }

  //Order Table, for orders page
  &__order-table {
    margin-top: 2rem;
    width: 100%;

    &-totals {
      font-weight: $s-font--weight-normal;

      //Extra specificity to trump styles above.
      &.is-right:first-of-type {
        text-align: right;
      }
    }
  }

  &__input {
    color: $s-color--font-1;

    &::placeholder {
      color: $s-color--grey-blue;
      font-style: italic;
    }
  }

  /*==== Media Queries ====*/
  @include t-media-query($s-small-up) {

    //styles for banner
    &__banner {
      width: 80%;

      &-image-container {
        padding-bottom: 31%;
      }
    }

    //Order Addresses, used on the order page.
    &__order-details {
      display: flex;
    }

    &__order-detail {
      margin-right: 2rem;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      justify-content: center;
      display: flex;
      flex-wrap: wrap;
    }

    &__sidebar {
      width: 28%;
      flex-shrink: 0;
      margin-right: 1.25rem;
    }

    &__main {
      flex: 1;
    }

    &__order-detail {
      margin-right: 4rem;
    }

    &__order-table {
      margin-top: 4rem;
    }
  }

  @include t-media-query($s-medium-up) {
    &__header {
      &-heading {
        margin-top: 4.288rem;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &__order-detail {
      margin-right: 6rem;
    }

    &__order-table {
      margin-top: 6rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__header {
      &-heading {
        margin-top: 6.125rem;
      }
    }
  }
}