.c-addresses {
  &__title {
    margin-bottom: t-to-rem(20);
  }

  &__form {
    display: none;
    max-width: 35.5rem;
    margin: t-to-rem(48) 0;

    &.is-visible {
      display: block;
    }

    .o-form__default-checkbox {
      margin: t-to-rem(20) 0;
    }
  }

  &__address {
    margin: t-to-rem(40) 0;

    &-title {
      margin-bottom: t-to-rem(20);
    }
  }

  &__btn {
    margin-right: t-to-rem(20);

    &-row {
      margin: t-to-rem(20) 0;
    }

    &:last-child {
      margin-right: unset;
    }
  }
}